.dcl.login-modal .dcl.option .info {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  margin-left: 16px;
}

.dcl.login-modal .dcl.option .info .title {
  font-weight: bold;
  color: var(--text);
}

.dcl.login-modal .dcl.option .info .subtitle {
  color: var(--secondary-text);
  margin-top: 4px;
}

.dcl.login-modal .dcl.option + .dcl.option {
  margin-top: 16px;
}

.dcl.login-modal .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 28px;
}

.dcl.login-modal .message {
  margin-top: 28px;
  text-align: center;
  display: block;
  color: var(--text);
  line-height: 1.5;
  opacity: 0.9;
}

.dcl.login-modal .error {
  opacity: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: var(--primary);
  margin: 20px 0;
  text-align: center;
}

.dcl.login-modal .error.visible {
  transition: opacity 0.25s ease;
  opacity: 1;
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}

.dcl.login-modal .loader-background {
  height: 100%;
  width: 100%;
  background-color: var(--background);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.5;
}
